.skew { position: relative; background-color: #FFF; margin: 90px 0 58px; }
.skew__border { position: absolute; top: -58px; left: 0; right: 0; }
.skew__border:before { content: ""; position: absolute; top: 0; right: 0; width: 0px; height: 0px; border-style: solid; border-width: 58px 50vw 58px 0; border-color: transparent #fff transparent transparent; }
.skew__border:after { content: ""; position: absolute; top: 0; left: 0; width: 0px; height: 0px; border-style: solid; border-width: 58px 0 58px 50vw; border-color: transparent transparent transparent #fff; }
.skew__border.mod_bottom { top: auto; bottom: 58px; }
