.skew__img { position: absolute; top: 50%; left: 50%; transform: translate(-50%, -50%); max-width: 100%; }
@media only screen and (max-width: 760px) { .skew__img { position: relative; top: 0; left: 0; transform: translate(0%, 0%); } }
.skew__btn { display: inline-block; height: 60px; padding: 0 26px; font-size: 30px; color: #000; background-color: #007b4e; line-height: 60px; font-family: "OpenSansLight", Arial, sans-serif; font-weight: normal; transition: 0.3s; }
.skew__btn:hover { color: #fff; }

